* {
	font-family: 'Roboto', sans-serif; }

header {
	position: relative;
	height: 50px; }

nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background: #212121;
	z-index: 2; }

.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none; }

.menu_link {
	font-weight: 300;
	font-size: 15px;
	color: #FFFFFF;
	text-decoration: none; }

.menu_item {
	border-right: 2px dotted rgb(29, 29, 29);
	padding-right: 45px; }

.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0, 0, 0, 0.6); }

.subheader_logo {
	display: block;
	max-width: 100%;
	margin-top: 16px; }

.subheader_official {
	margin-top: 6px;
	font-weight: 200;
	font-size: 13px;
	color: #FFFFFF;
	opacity: 0.5; }

.subheader_call {
	font-weight: 200;
	font-size: 12px;
	margin-top: 19px;
	text-align: right;
	color: #FFFFFF; }

.subheader_phone {
	display: block;
	text-align: right;
	margin-top: 8px;
	font-weight: 400;
	line-height: 12px;
	font-size: 22px;
	color: #FFFFFF;
	text-decoration: none; }

.subheader_btn {
	margin-top: 19px;
	width: 172px;
	height: 46px;
	background: linear-gradient(to top, #1EACC7 0%, #4CE2FF 100%);
	box-shadow: inset 0px -2px 0px rgba(0, 0, 0, 0.12);
	border-radius: 4px;
	font-weight: 400;
	font-size: 14px;
	display: block;
	align-items: center;
	text-align: center;
	text-transform: uppercase;
	color: #FFFFFF;
	text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21); }

.promo {
	min-height: 800px;
	background: url('../img/bg/uber_bg.png') center (center / cover) no-repeat;
	padding: 150px 0 186px 0; }

.promo_header {
	margin: 0;
	font-weight: 900;
	font-size: 60px;
	line-height: 60px;
	text-align: center;
	text-transform: uppercase;
	color: #FFFFFF; }

.promo_subheader {
	margin: 0;
	margin-top: 22px;
	font-weight: 700;
	font-size: 36px;
	line-height: 36px;
	text-align: center;
	text-transform: uppercase;
	color: #FFFFFF; }

.promo_descr {
	color: #FFFFFF;
	font-size: 20px;
	font-weight: 400;
	line-height: 24px;
	text-align: center;
	margin-top: 30px; }

.promo_btn {
	display: block;
	margin: 0 auto;
	margin-top: 120px;
	width: 259px;
	height: 63px;
	box-shadow: inset 0px -2px 0px rgba(0, 0, 0, 0.12);
	border-radius: 4px;
	background: linear-gradient(to top, #1EACC7 0%, #4CE2FF 100%);
	text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
	color: #FFFFFF;
	font-size: 18px;
	font-weight: 400;
	text-transform: uppercase; }

.reasons {
	padding: 18px 0 64px 0;
	background: #fff; }

.label {
	margin: 0 auto;
	width: 115px;
	height: 27px;
	padding: 7px 12px;
	background-color: #000000;
	font-weight: 400;
	font-size: 11px;
	text-transform: uppercase;
	color: #FFFFFF;
	text-align: center; }

.title {
	margin-top: 33px;
	font-weight: 700;
	font-size: 38px;
	text-align: center;
	text-transform: uppercase;
	color: #222222; }

.subtitle {
	color: #222222;
	font-weight: 400;
	font-size: 17px;
	text-align: center; }

.reasons_block {
	margin-top: 26px;
	display: flex;
	justify-content: center;
	align-items: center; }

.reasons_round {
	margin-right: 25px;
	padding: 5px 5px;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 116px;
	height: 116px;
	border-radius: 50%;
	background-color: #1EACC7; }

.reasons_descr {
	width: 400px; }

.reasons_subtitle {
	font-weight: 400;
	font-size: 17px;
	color: #1EACC7; }

.reasons_text {
	font-weight: 300;
	font-size: 15px;
	line-height: 21px;
	color: #202020; }

.mobileApp {
	padding: 48px 0 138px 0;
	min-height: 864px;
	background: url("../img/bg/mobile_bg.png") center (center / cover) no-repeat;
	&_item {} }

